@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

* {
  font-family: 'Roboto';

}

.calculator {
    width: 20rem;
    height: 30rem;
    display: grid;
    padding: 2%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border: solid 2px;
    background-color: rgb(255, 167, 167);
  }
  
  .keys-numbers {
    display: inline;
    direction: rtl;
  }

  .keys-operators {
    display: inline;
  }
  
  .calculator-keypad {
    padding-top: 1rem;
    display: inline;

  }

  .calculator-input {
    border-color: black;
    border: solid 1px;
    text-align: right;
    padding-right: 5%;
    padding-top: 5%;
    font-size: xx-large;
    background-color: white;
  }

  .result {
    min-height: 5rem;
    color: black;
  }